@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap');

*
{
    margin: 0;
    padding: 0;
    outline: none;
    text-decoration: none;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}

.thumbs-wrapper.axis-vertical
{
    display:none;
}

.footer
{
    font-size: 14px;
    background: #363636;
    color: white;
    text-align: center;
    width: 100%;
    padding: 20px 20px;
}

.navigator
{
    display: flex;
    background: #363636;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0px 3px 5px 0px rgb(0 0 0 / 25%);
    width: 100%;
    padding: 10px;
    box-sizing: border-box;
    color: white;
    font-weight: 600;
    font-size: 15px;
    position: sticky;
    z-index: 3;
}

/* Info */
.info
{
    display: flex;
    align-items: center;
    box-sizing: border-box;
}

.info-item
{
    padding: 0 5px;
}

.info-inner
{
    padding: 0 5px;
}

.info-sales
{
    display: flex;
    align-items: center;
}

.info-sales img
{
    width: 40px;
    margin-right: 10px;
}

.info-sales .name
{
    font-size: 12px;
}

.info-box
{
    display: flex;
    align-items: center;
    padding: 5px 15px;
    border: 1px solid white;
    border-radius: 10px;
    font-size: 12px;
}

/* Menu */
.menu-box
{
    display: flex;
    flex-direction: column;
    padding: 0 15px;
    align-items: center;
}

.menu-box img
{
    width: 30px;
    margin-bottom: 5px;
}

.menu-box span
{
    font-size: 11px;
}

/* Hamburger Menu */
.menu-toggle {
    display: none;
    flex-direction: column;
    height: 20px;
    justify-content: space-between;
    position: relative;
}

.menu-toggle input {
    position: absolute;
    width: 44px;
    height: 28px;
    left: -8px;
    top: -3px;
    opacity: 0;
    cursor: pointer;
    z-index: 3;
}

.menu-toggle span {
    display: block;
    width: 28px;
    height: 3px;
    background-color: #e8e7e5;
    border-radius: 5px;
    transition: all 0.5s;
}

/* Hamburger Menu Animation*/
.menu-toggle span:nth-child(2) {
    transform-origin: 0 0;
}

.menu-toggle span:nth-child(4) {
    transform-origin: 0 100%;
}

.menu-toggle input:checked~span:nth-child(2) {
    transform: rotate(45deg) translate(-1px, -1px);
}

.menu-toggle input:checked~span:nth-child(3) {
    opacity: 0;
    transform: scale(0);
}

.menu-toggle input:checked~span:nth-child(4) {
    transform: rotate(-45deg) translate(-1px, 0px);
    ;
}

/* Content */
.section
{
    display: block;
}
.baris
{
    display: flex;
    flex-wrap: wrap;
    position: relative;
    align-items: stretch;
}
.content-mobil
{
    flex: 0 0 70%;
    max-width: 70%;
}
.list-mobil
{
    padding: 20px 10px 0px 10px;
    height: 100%;
}
.mobil-menu
{
    margin-bottom: 20px;
    flex: 0 0 25%;
    max-width: 25%;
    padding-right: 10px;
    padding-left: 10px;
}
.mobil-menu .mobil
{
    padding: 10px;
    text-align: center;
    background-color: white;
    border-radius: 10px;
    transition: all .2s;
}
.mobil-menu .mobil:hover
{
    border-radius: 10px;
    box-shadow: 0px 3px 5px 0px rgb(0 0 0 / 25%);
}
.mobil-menu .mobil img
{
    width: 100%;
}
.mobil-info
{
    width: 100%;
    padding: 5px;
    border-radius: 10px;
    text-align: center;
    color: black;
    font-size: 13px;
}
.mobil_name
{
    font-size: 16px!important;
    font-weight: bold;
}
.mobil_price
{
    background-color: #363636;
    color: white;
    border-radius: 20px;
    font-weight: bold;
}

.content-testi
{
    flex: 0 0 30%;
    max-width: 30%;
}
.list-testi
{
    padding: 10px;
    background-color: #363636;
    color: #fff;
    height: 100%;
    border-left: 1px solid #ddd;
}
.box-testi
{
    flex: 0 0 50%;
    max-width: 50%;
}
.box-testi .testi
{
    padding: 10px;
    text-align: center;
    border-radius: 10px;
}
.box-testi .testi img
{
    width: 100%;
    border: 2px solid white;
}

.sales-testi
{
    width: 100%;
    text-align: center;
    margin-bottom: 20px;
}

.sales-testi img
{
    width: 60%;
    border: 2px solid white;
}

/* Detail */

.gambar-detail
{
    position: relative;
    overflow: hidden;
    display: block;
}

.gambar-detail img
{
    width: 100%;
    transition: all 0.5s;
    transform: scale(1);
    display: block;
}

.gambar-detail img:hover
{
    transform: scale(1.05);
}

/* Galery */

.gambar-galery
{
    margin-bottom: 20px;
    flex: 0 0 25%;
    max-width: 25%;
    padding-right: 10px;
    padding-left: 10px;
}

.gambar-galery img
{
    width: 100%;
    border-radius:10px
}


/* responsive */

@media (max-width:991px)
{
    .carousel-root
    {
        margin-top: 65px;
    }

    .navigator{
        position: fixed;
        top:0;
    }

    .menu
    {
        display: none;
    }

    .menu-toggle
    {
        display: flex;
    }

    .menu_open
    {
        opacity: 1!important;
        transform: translateX(0)!important;
    }

    .info.menu
    {
        position: absolute;
        right: 0;
        top: 66px;
        width:80px;
        height:100vh;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        background: #363636;
        padding: 10px;
        z-index: 1;
        transform: translateX(100%);
        transition: all 1s;
        opacity: 0;
        box-shadow: 0px 3px 5px 0px rgb(0 0 0 / 25%);
    }
    
    .info.menu .info-item
    {
        width: 100%;
        display: block;
        padding: 0;
        margin-bottom:30px
    }

    .info.menu .menu-box
    {
        padding: 0;
    }

    .info.menu .info-inner{
        padding: 0;
    }

    .info.menu .menu-box{
        display: flex;
        flex-direction: column;
    }

    .content-mobil
    {
        flex: 0 0 100%;
        max-width: 100%;
    }

    .content-testi
    {
        flex: 0 0 100%;
        max-width: 100%;
    }

    .mobil-menu
    {
        flex: 0 0 50%;
        max-width: 50%;
    }

    .gambar-galery
    {
        flex: 0 0 50%;
        max-width: 50%;
    }
}